.intro__item-link {
  display: block;
  font-size: 19px;
  line-height: 24px;
  font-weight: bold;
  color: #ffffff;
  padding-left: 29px;
  width: 150px;
}

.intro__item:active {
  .intro__item-link--light {
    color: #97e2d2;
  }

  .intro__item-link--dark {
    color: #8dd5c9;
  }
}

.intro__item--dark:hover,
.intro__item--light:hover {
  transform: translateY(-5px);
}


@media only screen and (min-width: @tablet-width) {
  .intro {
    background-image: url("../img/hero-tablet@1x.jpg");
    background-repeat: no-repeat;
    background-size: 1150px auto;
    background-position: center top;
    position: relative;

    @media (min-resolution: @retina-dpi), (min-resolution: @retina-dppx) {
      background-image: url("../img/hero-tablet@2x.jpg");
    }
  }

  .intro__title {
    font-size: 43px;
    line-height: 50px;
    text-align: center;

    width: auto;
    padding-top: 153px;
    margin-bottom: 252px;
  }

  .intro__list {
    .wrapper-tab();
    display: flex;
  }

  .intro::after {
    position: absolute;
    content: " ";
    bottom: -13px;
    left: 0;
    width: 100%;
    height: 24px;
    background: url("../img/bg-zigzag-fill.svg");
    background-size: 768px 26px;
  }

  .intro__item-link {
    font-size: 20px;
    line-height: 30px;

    padding-top: 3px;
    padding-left: 35px;
  }

  .intro__item {
    padding-top: 23px;
    width: 50%;
  }

  .intro__item-link--dark::after {
    top: 31px;
    right: 34px;
  }

  .intro__item-link--light::after {
    top: 29px;
    right: 16px;
    height: 62px;
  }
}


@media only screen and (min-width: @desktop-width) {
  .intro {
    padding-top: 1px;
    margin-top: -1px;
    background-image: url("../img/hero-desktop@1x.jpg");
    background-repeat: no-repeat;
    background-position: center -2px;
    background-size: 1150px auto;

    @media (min-resolution: @retina-dpi), (min-resolution: @retina-dppx) {
      background-image: url("../img/hero-desktop@2x.jpg");
      background-size: 1150px auto;
    }
  }

  .intro::after {
    bottom: -6px;
    left: -16px;
    background-size: 1150px 34px;
  }

  .intro__title {
    font-size: 53px;
    line-height: 57px;
    text-align: center;
    // margin-top: 152px;
    margin-bottom: 239px;
  }

  .intro__list {
    .wrapper-desk();
    display: flex;
  }
}
