.reviews__wrapper {
  .wrapper-mob();
  position: relative;
  margin-bottom: 44px;
}

.reviews__title {
  .title();
  padding-top: 35px;
}

.reviews__item-text {
  font-size: 15px;
  line-height: 30px;
  color: @col-grey;
  text-align: left;

  margin: 0;
  padding-top: 3px;
  margin-bottom: 27px;
}

.reviews__item-name {
  font-weight: bold;
  font-size: 17px;
  line-height: 24px;
  font-style: normal;

  margin-bottom: 3px;
  display: block;
}

.reviews__item-link {
  font-size: 17px;
  line-height: 30px;
  color: @col-grey;
}

.reviews__written {
  font-size: 18px;
}

.reviews__control-wrap {
  display: flex;
  margin-bottom: 37px;
  margin-top: 30px;
}

.reviews__control {
  width: 50%;

  background: none;
  border: 1px solid #ececec;
  height: 52px;
  position: relative;

  &:hover {
    opacity: 0.6;
  }

  &:active {
    opacity: 0.3;
  }
}

.reviews__control--early--svg {
  position: absolute;
  top: 0;
  left: 0;
}

.reviews__control--next--svg {
  position: absolute;
  top: 0;
  left: 10px;
}


@media only screen and (min-width: @tablet-width) {
  .reviews {
    // .wrapper-tab();
    position: relative;
    background-image: url("../img/review@1x.jpg");
    background-repeat: no-repeat;
    background-size: 350px 458px;


    @media (min-resolution: @retina-dpi), (min-resolution: @retina-dppx) {
      background-image: url("../img/review@2x.jpg");
    }
  }

  .reviews::after {
    height: 32px;

    background-size: auto 15px;
    background-position: 17px 3px;
    padding-top: 50px;
    bottom: -84px;
    left: 0;

    transform: rotate(180deg);
  }

  .reviews__wrapper {
    .wrapper-tab();
  }

  .reviews__title {
    .title-tab();
    margin-left: -5px;
    padding-top: 77px;
  }

  .reviews__item-text {
    font-size: 17px;
    line-height: 30px;

    padding-top: 13px;
    margin-bottom: 56px;
  }

  .reviews__item-name {
    margin-bottom: 5px;
    font-size: 18px;
  }

  .reviews__control {
    border: none;
  }

  .reviews__item {
    margin-bottom: 46px;
  }

  .reviews__written {
    font-size: 17px;

    position: absolute;
    top: 80px;
    right: 0;
    width: 175px;
  }

  .button {
    padding: 14px 0;
  }

  .reviews__control-wrap {
    position: relative;
    width: 200px;
  }

  .reviews__control--early {
    position: absolute;
    bottom: 47px;
    right: -376px;

    width: 100px;
  }

  .reviews__control--next {
    position: absolute;
    right: -463px;
    bottom: 47px;

    width: 100px;
  }
}

