.week-product {
  .wrapper-mob();
}

.week-product__title {
  font-size: 25px;
  line-height: 30px;

  margin-top: 27px;
  margin-bottom: 4px;
}

.week-product__label {
  font-size: 17px;
  line-height: 20px;
  font-weight: bold;
  text-transform: uppercase;
  color: @col-light;

  margin-top: 27px;
}

.week-product__description {
  font-size: 15px;
  line-height: 24px;
  color: @col-grey;

  margin-top: 20px;
  margin-bottom: 1px;
}

.week-product__params {
  padding-top: 12px;
  padding-bottom: 14px;
}

.week-product__params-row {
  padding-bottom: 11px;
  padding-top: 8px;
  border-bottom: 1px solid #ececec;
  display: flex;
}

.week-product__params-row:first-child {
  padding-top: 0;
}

.week-product__params-key,
.week-product__params-value {
  display: inline;
  font-size: 15px;
  line-height: 20px;
  color: @col-grey;

  margin: 0;
}

.week-product__params-key {
  margin-right: 27px;
  width: 70px;
}

.week-product__image {
  margin-top: 13px;
}

.week-product__cost {
  font-size: 17px;
  line-height: 24px;
  font-weight: bold;
  color: @col-grey;

  text-align: center;
  margin-bottom: 26px;
  margin-top: 24px;
}

